.contenedor-diagrama {
  width: 100%;
  height: 600px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 background-color:  rgba(0, 56, 68, 1) ;
}

.texto-interseccion {
  font-size: 14px;
  font-weight: bold;
  fill: #ffffff;
  text-anchor: middle;
}

circle {
  cursor: pointer;
}
.texto-conjunto {
  font-size: 25px;
  font-weight: bold;
  text-anchor: middle; /* Centrar el texto horizontalmente */
  fill: rgb(255, 255, 255); /* Color del texto */
  cursor: pointer; /* Cambiar cursor al pasar sobre el texto */
}

.texto-hover {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  padding: 5px;
  text-align: center;
  box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.2);
}
.svg-diagrama{
  overflow-x: auto;
}
@media (max-width: 1024px){
  .contenedor-diagrama {
    display: none;
  }
}
@media(width <= 768px){
  .contenedor-diagrama {
    display: none;
  }
}